// Load json files from path
const loadJSON = async function (path) {
  // Handle both relative and absolute URLs
  const url = new URL(path, chrome.runtime.getURL('')).toString()
  const response = await fetch(url, {
    cache: 'no-cache'
  })
  if (!response.ok) {
    const errorString = await response.text()
    throw new Error(`Load JSON error \nURL: ${url} \nResponse status code:${response.status} \nError message: ${errorString}`)
  }
  const json = await response.json()
  return json
}

// Load extension env configuration. config.json is generated in build time.
const configPromise = new Promise((resolve, reject) => {
  Promise.all([
    loadJSON('config.json'),
    loadJSON('manifest.json')
  ]).then(([configJSON, manifestJSON]) => {
    resolve({
      VERSION: manifestJSON.version,
      ...configJSON,
    })
  }).catch((exception) => {
    console.error(`ConfigError: config.json ${exception.message}`)
    reject(exception)
  })
})

let lastFeaturesConfigFetch = 0
let featuresConfigPromise = null
let cachedConfig = null
export const loadFullConfig = async () => {
  const extensionConfig = await configPromise
  const currentTime = new Date().getTime()
  // "Invalidate" cache every 30 mins
  if (featuresConfigPromise === null || lastFeaturesConfigFetch + 1800000 < currentTime) {
    lastFeaturesConfigFetch = currentTime
    const { SITE_HOSTNAME, BUILD_TARGET, VERSION } = extensionConfig
    featuresConfigPromise = loadJSON(`${SITE_HOSTNAME}/plugin_assets/${VERSION}/${BUILD_TARGET}_features.json`)
  }
  const featureConfig = await featuresConfigPromise
    // Fallback to extension's internal feature config
    .catch(exception => {
      console.error(exception)
      return {}
    })
  return cachedConfig = {
    ...extensionConfig,
    ...featureConfig,
  }
};

// Returns the promise for the partial ENV config without the feature toggles
// Added for better performance for places where we only need the env vars
export const getEnvConfig = () => {
  return configPromise
}

// Memoize config object for synchronous reads resulting in better load performance
// Return cached config if exists and fetch fresh version of config in the background
export const getCachedConfig = () => {
  loadFullConfig()
  if (cachedConfig) {
    return cachedConfig
  }
  return null
};
