const setTrackerUserID = function (userID) {
  ga((tracker) => {
    tracker.get('userId') !== userID
    ga('set', 'userId', userID)
    console.info(`Set analytics userID to ${userID}`)
  })
  if (amplitude.getInstance()) {
    amplitude.getInstance().setUserId(userID)
  }
}

const setTrackerDeviceID = function (deviceID) {
  if (amplitude.getInstance()) {
    console.info(`Set analytics deviceID to ${deviceID}`)
    amplitude.getInstance().setDeviceId(deviceID)
  }
}

const setupGoogleAnalytics = (config) => {
  if (config.GOOGLE_ANALYTICS_ID) {
    ga('create', config.GOOGLE_ANALYTICS_ID, 'auto')
    ga('set', 'checkProtocolTask', () => {})
    ga('require', 'displayfeatures')
    ga('require', 'ecommerce')
  } else {
    console.info('Google Analytics ID not found')
  }
}

const setupAmplitude = (config) => {
  if (config.AMPLITUDE_ID) {
    const amplitudeInstance = amplitude.getInstance()
    amplitudeInstance.init(config.AMPLITUDE_ID)
    amplitudeInstance.onInit(function (instance) {
      // This is all kinds of horrible, but there does not seem to be any other way of overriding the native aplitude browser detection
      if (navigator.brave && instance._ua && instance._ua.browser) {
        instance._ua.browser.name = 'Brave'
      }
    })
    amplitudeInstance.setVersionName(config.VERSION);
  } else {
    console.info('Amplitude ID not found')
  }
}

const setupPromise = new Promise((resolve, reject) => {
  Promise.all([getEnvConfig(), getUserID(), getDeviceID()])
  .then(([config, {authID, installID}, {deviceID}]) => {
    setupGoogleAnalytics(config)
    setupAmplitude(config)
    setTrackerUserID(authID || installID)
    setTrackerDeviceID(deviceID)
    resolve()
  }).catch((exception) => {
    reject(exception)
  })
})

export const identify = async function (authID, {userProperties} = {}) {
  if (authID) {
    await setStoredID('authID', authID)
  } else {
    await removeStoredID('authID')
  }

  const { installID } = authID ? {} : await getStoredID('installID')
  const userID = authID || installID

  setTrackerUserID(userID)

  if (userProperties) {
    const identify = new amplitude.Identify()
    Object.entries(userProperties).forEach(([key, value]) => {
      identify.set(key, value)
    })
    amplitude.identify(identify)
  }
  if (authID) {
    return { authID }
  } else {
    return { installID }
  }
};

export const pageview = async function (path, args = {}) {
  return setupPromise.then(() => {
    if (ga) {
      ga('send', 'pageview', {
        page: path,
        ...args
      })
    }
    if (amplitude) {
      const eventType = `Viewed ${args.title || 'Unknown'} Page`
      amplitude.getInstance().logEvent(eventType, {
        url: path,
        ...args
      });
    }
  })
};

export const transaction = async function (options) {
  const {
    productId,
    productName,
    price,
    quantity = 1,
    categoryName,
    store,
    sku,
    rank,
    score,
    label
  } = options
  if (ga) {
    const transactionId = hash({
      ...options,
      time: Date.now().toString()
    })
    ga('ecommerce:addTransaction', {
      'id': transactionId,
      'affiliation': store,
      'revenue': (price * quantity).toString(),
      'currency': 'USD',
    })
    ga('ecommerce:addItem', {
      'id': transactionId,
      'name': productName,
      'sku': productId,
      'category': categoryName,
      'price': price,
      'quantity': quantity,
    })
    ga('ecommerce:send')
  }
  if (amplitude) {
    let revenue = new amplitude.Revenue()
      .setPrice(price)
      .setProductId(productId)
      .setQuantity(quantity)
      .setEventProperties({
        'id': productId,
        'name': productName,
        categoryName,
        sku,
        rank,
        score,
        label,
      })
    amplitude.getInstance().logRevenueV2(revenue)
  }
}

export const track = async function (eventType, args) {
  return setupPromise.then(() => {
    if (ga) {
      ga('send', 'event', {
        eventAction: eventType,
        ...args
      })
    }
    if (amplitude) {
      amplitude.getInstance().logEvent(eventType, args);
    }
  })
};

// Ping analytics once per day to measure install retention
// Memoize lastDailyPing to prevent having to run get every time
let lastDailyPingMemo = 0
// Track if ping is running to avoid race conditions
let pingRunning = false
export const ping = async function () {
  try {
    // Exit early if ping is already running
    if (pingRunning) {
      return
    }
    // Prevent new ping calls from running
    pingRunning = true
    const currentTime = new Date().getTime()
    // Get persisted lastDailyPing value
    if (!lastDailyPingMemo) {
      // Ping value must be stored so the value persists between background script runs
      const { lastDailyPing = 0 } = await getLocal('lastDailyPing') || {}
      lastDailyPingMemo = lastDailyPing
    }
    // Exit if a day has not passed since last ping
    if (lastDailyPingMemo + 86400000 < currentTime) {
      // Update lastDailyPing to prevent it from running for a day
      await setLocal({ lastDailyPing: currentTime })
      // Update memo to new set time, should be done after storage to prevent it from being updated in case of set error
      lastDailyPingMemo = currentTime
      // Send tracking message, should be done last to prevent duplicate calls in case of error
      await track('daily ping', {
        eventCategory: 'background',
      })
    }
    // Allow new ping calls to run
    pingRunning = false
  } catch (error) {
    console.error('daily ping error', error)
    await track('daily ping error', {
      eventCategory: 'background',
      message: error.message,
    })
    // Allow new ping calls to run in case of error
    pingRunning = false
  }
}
