import omit from 'lodash/omit'
import { v4 as uuidv4 } from 'uuid'

const hash = require('hash-sum')

// Generic storage promise wrappers

const wrapExecutor = (executor) => {
  return (param) => {
    return new Promise((resolve, reject) => {
      executor(param, (result) => {
        if (chrome.runtime && chrome.runtime.lastError) {
          reject(chrome.runtime.lastError)
        } else {
          resolve(result)
        }
      })
    })
  }
}

export const getLocal    = wrapExecutor(chrome.storage.local.get.bind(chrome.storage.local))
export const setLocal    = wrapExecutor(chrome.storage.local.set.bind(chrome.storage.local))
export const removeLocal = wrapExecutor(chrome.storage.local.remove.bind(chrome.storage.local))
export const getSync     = wrapExecutor(chrome.storage.sync.get.bind(chrome.storage.sync))
export const setSync     = wrapExecutor(chrome.storage.sync.set.bind(chrome.storage.sync))
export const removeSync  = wrapExecutor(chrome.storage.sync.remove.bind(chrome.storage.sync))

// ID related state management
const getRandomToken = function () {
  var randomPool = new Uint8Array(32);
  crypto.getRandomValues(randomPool);
  var hex = '';
  for (var i = 0; i < randomPool.length; ++i) {
      hex += randomPool[i].toString(16);
  }
  return hex;
}

// This memoization is necessary to prevent race conditions
const generatedIDs = {}

export const getStoredID = function (idKey) {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.get([idKey], (result = {}) => {
      if (result[idKey]) {
        resolve({[idKey]: result[idKey]})
      } else {
        if (!generatedIDs[idKey]) {
          generatedIDs[idKey] = getRandomToken()
        }
        const idValue = generatedIDs[idKey]
        chrome.storage.sync.set({[idKey]: idValue}, () => {
          resolve({[idKey]: idValue})
        })
      }
    })
  })
};

export const setStoredID = function (idKey, idValue) {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.set({[idKey]: idValue}, () => {
      resolve({[idKey]: idValue})
    })
  })
};

export const removeStoredID = function (idKey) {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.remove(idKey, () => {
      resolve()
    })
  })
};

export const getUserID = function () {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.get(['authID'], ({authID} = {}) => {
      if (authID) {
        resolve({authID})
      } else {
        getStoredID('installID').then(({installID} = {}) => {
          resolve({installID})
        })
      }
    })
  })
};

export const getDeviceID = function () {
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(['deviceID'], ({deviceID} = {}) => {
      if (deviceID) {
        resolve({deviceID})
      } else {
        if (!generatedIDs['deviceID']) {
          generatedIDs['deviceID'] = uuidv4()
        }
        const idValue = generatedIDs['deviceID']
        chrome.storage.local.set({deviceID: idValue}, () => {
          resolve({deviceID: idValue})
        })
      }
    })
  })
};

export const setDeviceID = function (idValue) {
  return new Promise((resolve, reject) => {
    chrome.storage.local.set({deviceID: idValue}, () => {
      resolve({deviceID: idValue})
    })
  })
};

export const removeDeviceID = function () {
  return new Promise((resolve, reject) => {
    chrome.storage.local.remove('deviceID', () => {
      resolve()
    })
  })
};

// declare type PredictionState = {
//   product_type_id: string
//   category_name: string
//   prediction_time: number
//   score: string
//   rank: number
//   label: string
// }
// declare type PredictionStateIndex = {
//   [key: string]: PredictionState
// }
export const setPredictions = function (newPredictions) {
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(['predictions'], ({predictions = {}} = {}) => {
      const newPredictionKeys = Object.keys(newPredictions)
      // Merge data with previous predictions if it exists to preserve data like rank
      newPredictionKeys.forEach((key) => {
        const newPrediction = newPredictions[key] || {}
        const oldPrediction = predictions[key] || {}
        // Overwrite old prediction if new prediction is a new category
        if (oldPrediction.category_name === newPrediction.category_name) {
          predictions[key] = {...oldPrediction, ...newPrediction}
        } else {
          predictions[key] = newPrediction
        }
      })
      chrome.storage.local.set({predictions: predictions}, () => {
        resolve(predictions)
      })
    })
  })
};

export const getPredictions = function () {
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(['predictions'], ({predictions = {}} = {}) => {
      resolve(predictions)
    })
  })
};

export const removePredictions = function (predictionKeys) {
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(['predictions'], ({predictions = {}} = {}) => {
      const filteredPredictions = omit(predictions, predictionKeys)
      chrome.storage.local.set({predictions: filteredPredictions}, () => {
        resolve(filteredPredictions)
      })
    })
  })
};

