// Add hashed order id
export const addOrderCheck = function (orderCheck) {
  const orderCheckHash = hash(orderCheck)
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(['orderChecks'], ({orderChecks = []} = {}) => {
      const combinedOrderCheckHashes = Array.from(new Set([...orderChecks, orderCheckHash]))
      chrome.storage.local.set({orderChecks: combinedOrderCheckHashes}, () => {
        resolve(orderCheck)
      })
    })
  })
};

// Check order id existence
export const hasOrderCheck = function (orderCheck) {
  const orderCheckHash = hash(orderCheck)
  return new Promise((resolve, reject) => {
    chrome.storage.local.get(['orderChecks'], ({orderChecks = []} = {}) => {
      resolve(orderChecks.includes(orderCheckHash))
    })
  })
};

export const addDisabledSite = function (domainString) {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.get(['disabledSites'], ({disabledSites = []} = {}) => {
      if (disabledSites.indexOf(domainString) === -1) {
        disabledSites.push(domainString)
      }
      chrome.storage.sync.set({disabledSites}, () => {
        resolve(disabledSites)
      })
    })
  })
};

export const removeDisabledSite = function (domainString) {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.get(['disabledSites'], ({disabledSites = []} = {}) => {
      if (disabledSites.indexOf(domainString) !== -1) {
        disabledSites.splice(disabledSites.indexOf(domainString), 1)
      }
      chrome.storage.sync.set({disabledSites}, () => {
        resolve(disabledSites)
      })
    })
  })
};

export const getDisabledSites = function () {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.get(['disabledSites'], ({disabledSites = []} = {}) => {
      resolve(disabledSites)
    })
  })
};

// declare type PurchaseMetaState = {
//   isOnSaleProductCount: number
//   productCount: number
//   purchaseCategoryList: string[]
//   totalSpend: number
// }
export const getPurchaseMeta = function () {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.get(['purchaseMeta'], ({purchaseMeta = {}} = {}) => {
      resolve(purchaseMeta)
    })
  })
}

export const setPurchaseMeta = function (purchaseFields) {
  return new Promise((resolve, reject) => {
    chrome.storage.sync.get(['purchaseMeta'], ({purchaseMeta = {}} = {}) => {
      const { categoryName, isOnSale, price } = purchaseFields
      const { isOnSaleProductCount = 0, productCount = 0, purchaseCategoryList = [], totalSpend = 0 } = purchaseMeta
      const updatedPurchaseMeta = {
        isOnSaleProductCount: isOnSale ? isOnSaleProductCount + 1 : isOnSaleProductCount,
        productCount: productCount + 1,
        purchaseCategoryList: categoryName ? [...new Set([...purchaseCategoryList, categoryName])] : purchaseCategoryList,
        totalSpend: totalSpend + (price || 0),
      }
      chrome.storage.sync.set({ purchaseMeta: updatedPurchaseMeta }, () => {
        resolve(updatedPurchaseMeta)
      })
    })
  })
}

