import { getEnvConfig } from './config'
import { buildCategorySearchQueryBundle } from './utils'
import { track } from './analytics'

export const loadQuery = async function (host, queryName, queryBundle) {
  const apiUrl = `${host}/${queryName}`
  try {
    const fetchOptions = {
      body: JSON.stringify(queryBundle),
      headers: {
        'Content-type': 'application/json',
      },
      method: 'POST',
    }

    const response = await fetch(apiUrl, fetchOptions)
    return await response.json()
  } catch (error) {
    console.error('loadQuery Error:', apiUrl, queryBundle, error)
    track('fetch error', {
      eventCategory: 'background',
      url: apiUrl,
      error: error.message,
      query: JSON.stringify(queryBundle),
    })
  }
};

// keep navigation pre-fetched category search in memory
let categorySearchMemoize = {
  query: '',
  categorySearchPromise: null,
}

export const searchCategory = async function (searchParam) {
  const { query, categorySearchPromise } = categorySearchMemoize
  if (query === searchParam) {
    return categorySearchPromise
  } else {
    const queryBundle = buildCategorySearchQueryBundle(searchParam)
    const { BATCH_HOSTNAME } = await getEnvConfig()
    const searchPromise = loadQuery(BATCH_HOSTNAME, 'category_search', queryBundle)
    categorySearchMemoize = {
      query: searchParam,
      categorySearchPromise: searchPromise,
    }
    return searchPromise
  }
};
