// Sentry error tracking setup, must be set up as early as possible to capture errors
import * as Sentry from '@sentry/browser'
import { Integrations } from '@sentry/tracing'

try {
  if (SENTRY_DSN) {
    Sentry.init({
      dsn: SENTRY_DSN,
      release: SENTRY_RELEASE,
      integrations: [
        new Integrations.BrowserTracing(),
      ],
      tracesSampleRate: 1.0,
    })
  }
} catch (error) {
  console.error('Sentry setup error', error)
}

import { getEnvConfig } from './config'
import { identify, track, ping } from './analytics'
import * as handlers from './handlers'
import { getDeviceID, getUserFlags, setUserFlags } from './state'

// Attach background message handlers
const dispatcher = function (request, sender, sendResponse) {
  ping()
  console.info(`Received Message Type: ${request.type}`)
  const handler = handlers[request.type]
  if (handler) {
    return handler(request, sender, sendResponse)
  } else {
    console.info(`Message Type Not Found: ${request.type}`)
  }
}

chrome.runtime.onMessage.addListener(dispatcher)
chrome.runtime.onMessageExternal.addListener(dispatcher)

// Take user to post-install welcome page
chrome.runtime.onInstalled.addListener((details) => {
  Promise.all([getEnvConfig(), getUserFlags()])
  .then(([{ VERSION, SITE_HOSTNAME }, { hasInstalled }]) => {
    if (details.reason == 'install') {
      if (!hasInstalled) {
        track('installed extension', {
          eventCategory: 'background',
          eventLabel: VERSION,
          version: VERSION
        })
        setUserFlags({ hasInstalled: true })
        chrome.tabs.create({ url: `${SITE_HOSTNAME}/extension/welcome` })
      } else {
        track('reinstalled extension', {
          eventCategory: 'background',
          eventLabel: VERSION,
          version: VERSION
        })
      }
    }
  })
})

// Set uninstall url
Promise.all([getEnvConfig(), identify(), getDeviceID()])
.then(([{ SITE_HOSTNAME }, { authID, installID }, { deviceID }]) => {
  if (chrome.runtime.setUninstallURL) {
    let redirectUrl
    try {
      const uninstallUrl = encodeURIComponent(`/extension/uninstall`)
      const userId = authID || installID
      const eventType = encodeURIComponent('uninstalled extension')
      redirectUrl = `${SITE_HOSTNAME}/go?url=${uninstallUrl}&user_id=${userId}&device_id=${deviceID}&event_type=${eventType}&event_category=background`
      chrome.runtime.setUninstallURL(redirectUrl, () => {
        if (chrome.runtime.lastError) {
          console.error(chrome.runtime.lastError.message, redirectUrl)
          track('set uninstall url error', {
            message: chrome.runtime.lastError.message,
            url: redirectUrl,
          })
        }
      })
    } catch (error) {
      console.error(error, redirectUrl)
      track('set uninstall url error', {
        message: error.message,
        url: redirectUrl,
      })
    }
  }
})

chrome.browserAction.setIcon({
  path: {
    '16': '../assets/images/logo-disabled-16.png',
    '48': '../assets/images/logo-disabled-48.png',
    '128': '../assets/images/logo-disabled-128.png',
  }
})

// Safari currently has a bug that doesn't allow loading iframes in the extension's popup
// The temp workaround is to open Lustre's homepage in a new tab to keep things simple
// When the bug is fixed this change should be reverted and we should be able to use the manifest's default_popup again
// The check for management's existence is the best way I found to identify Safari as it's the only browser that's missing it entirely. unfortunately only FF supports runtime.getBrowserInfo atm
if ('management' in chrome) {
  // Not Safari
  chrome.browserAction.setPopup({popup: chrome.runtime.getURL('/popup/popup.html')})
} else {
  // For Safari
  chrome.browserAction.onClicked.addListener((tab) => {
    chrome.tabs.create({'url': 'https://lustre.ai'})
  })
}

chrome.tabs.onUpdated.addListener((tabId, {status}, tabState) => {
  const message = {
    type: 'tab_updated',
    status
  }
  chrome.tabs.sendMessage(tabId, message)
})
