import { identify, transaction, pageview as trackPageview, track as trackEvent } from './analytics'
import { getCachedConfig, getEnvConfig, loadFullConfig } from './config'
import { loadQuery, searchCategory } from './api'
import {
  getUserID,
  setPredictions, getPredictions, removePredictions,
  setProducts, getProducts, addOrderCheck, hasOrderCheck,
  addDisabledSite, removeDisabledSite, getDisabledSites,
  setLookups, getLookups, getPurchaseMeta, setPurchaseMeta,
} from './state'

export const get_config = (request, sender, sendResponse) => {
  const cachedConfig = getCachedConfig()
  if (cachedConfig) {
    sendResponse(cachedConfig)
  // fallback to reload config in case the object is not in memory
  } else {
    loadFullConfig().then((result) => {
      sendResponse(result)
    })
    return true
  }
};

export const set_authenticated_user_id = ({authID, userProperties}, sender, sendResponse) => {
  identify(authID, {userProperties}).then((result) => {
    sendResponse(result)
  })
  return true
};

