export const check_order = ({skus, orderIds, purchaseIds}, sender, sendResponse) => {
  const orderCheck = {skus, orderIds, purchaseIds}
  hasOrderCheck(orderCheck).then(hasOrder => {
    addOrderCheck(orderCheck)
    sendResponse(hasOrder)
  })
  return true
}

export const set_purchase = (request, sender, sendResponse) => {
  Promise.all([getPredictions(), getProducts(), getLookups()])
  .then(([predictions, products, lookups]) => {
    const product = products[request.sku] || {}
    const lookup = lookups[request.sku] || {}
    const productTypeId = lookup.product_type_id
    const prediction = predictions[productTypeId] || {}
    if (!products[request.sku]) {
      sendResponse(null)
      return
    }
    const predictionFields = {
      productId: prediction.product_type_id,
      productName: prediction.product_name,
      price: product.price,
      store: product.store,
      sku: product.sku,
      categoryName: prediction.category_name,
      rank: prediction.rank,
      score: prediction.score,
      label: prediction.label,
      isOnSale: prediction.is_on_sale,
    }
    const transactionPromise = transaction({
      ...predictionFields,
      quantity: 1,
    })
    const recommendationPromise = (() => {
      if (predictions[productTypeId]) {
        removePredictions([prediction.product_type_id])
        return trackEvent('purchased recommended product', {
          ...predictionFields,
          eventCategory: `${product.store} checkout`,
          eventLabel: prediction.category_name,
          eventValue: Math.round(product.price*100),
          predictionTime: prediction.prediction_time,
        })
      }
    })()
    setPurchaseMeta(predictionFields)
    Promise.all([transactionPromise, recommendationPromise])
    .then(([transaction, recommendation]) => {
      if (recommendation) {
        sendResponse({recommended: true})
      } else {
        sendResponse({recommended: false})
      }
    })
  })
  return true
};

export const pageview = (request, sender, sendResponse) => {
  trackPageview(...request.arguments).then(() => {
    sendResponse({})
  })
  return true
};

export const track = (request, sender, sendResponse) => {
  trackEvent(...request.arguments).then(() => {
    sendResponse({})
  })
  return true
};

export const load_query = (request, sender, sendResponse) => {
  const { host, queryName, queryBundle } = request
  loadQuery(host, queryName, queryBundle).then((response) => {
    sendResponse(response)
  })
  return true
};

export const category_search = (request, sender, sendResponse) => {
  const { searchParam } = request
  const promise = searchCategory(searchParam)
  promise.then((result) => {
    sendResponse(result)
  })
  return true
};

export const set_extension_state = (request, sender, sendResponse) => {
  const { enabled, domain } = request
  if (!sender.tab) {
    sendResponse({enabled: false})
    return false
  }
  if (enabled) {
    getEnvConfig().then(({BUILD_TARGET}) => {
      let modifier = '-development'
      switch (BUILD_TARGET) {
        case 'development':
        case 'staging':
          modifier = `-${BUILD_TARGET}`
          break
        case 'production':
          modifier = ''
          break
      }
      chrome.browserAction.setIcon({
          path: {
            '16': `../assets/images/logo${modifier}-16.png`,
            '48': `../assets/images/logo${modifier}-48.png`,
            '128': `../assets/images/logo${modifier}-128.png`,
          },
          tabId: sender.tab.id
        }, () => {
        sendResponse({enabled})
      })
    })
  } else {
    chrome.browserAction.setIcon({
        path: {
          '16': '../assets/images/logo-disabled-16.png',
          '48': '../assets/images/logo-disabled-48.png',
          '128': '../assets/images/logo-disabled-128.png',
        },
        tabId: sender.tab.id
      }, () => {
      sendResponse({enabled})
    })
  }
  return true
};
