const hash = require('hash-sum')

export const extractAmazonSearchParam = function (urlString) {
  const urlObj = new URL(urlString)
  const params = new URLSearchParams(urlObj.search)
  return params.get('k')
};

export const buildCategorySearchQueryBundle = function (searchQuery) {
  const search = decodeURIComponent(searchQuery.replace(/\+/g, ' '))
  const queryKey = search.replace(/\s+/g, '__')
  const bundle = {
    "queries": {
      "category_search": {
        [queryKey]: {
          "name": search
        }
      }
    }
  }
  return bundle
};
